.testimonial {
    background-color: $verde;
    font-size: 2.4rem;
    padding: 5rem;
    color: $blanco;
    border-radius: 4rem;
    display: grid;
    grid-template-rows: 2fr 2fr;
    // display: flex;
    opacity: 300rem;
    margin: 10rem;

    h4 {
        text-align: left;
    }
    blockquote {
        // padding-left: 5rem;
        position: relative;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr ;
        // padding: 3rem;
        margin-left: 2rem;
        margin-right: 2rem;
        // height: auto;

        // &::before {
        //     content: '';
        //     background-image: url(../img/comilla.svg);
        //     background-repeat: no-repeat;
        //     width: 6rem;
        //     height: 8rem;
        //     display: block;
        //     position: absolute;
        //     left: -2rem;
        // }
    }

    p {
        text-align: right;
        color: $blanco;
    }
}
.espacio {
    display: grid;
}
.img {
    border-radius: 2rem;
    padding: 1rem;
    height: auto;
}
.fondo {
    // background-image: url(../../img/index.PNG);
    background-position: 50%;
    background-size: cover;
}
.index {

    .formulari {
        margin: 0 auto 5rem;
    }
    .mikepijo {
        display: flex;
        .mikeeo{
          background-color: $rojo;
        }
        .boton {
          width: 40%;
          
        }
      }
    h1 {
        font-family: Verdana, Geneva, Tahoma, sans-serif;
    }
    // display: flex;
    background-color: $verde;
    margin: 4rem;
    border-radius: 5rem;
    // padding: 4.5rem;
    

    h4 {
        text-align: left;
        text-transform: uppercase;
        font-size: 4.4rem;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: 100;
        transform:scaleY(1.1);
        margin: 0;
        margin-left: 3rem;
        margin: 0 0 0 0rem;
    }
}



@include tablet {
    
    .index {
        // display: flex;
        background-color: $verde;
        margin: 8rem;
        border-radius: 5rem;
        padding: 4.5rem;
    
        h4 {
            text-align: left;
            text-transform: uppercase;
            font-size: 4.4rem;
            font-family: Verdana, Geneva, Tahoma, sans-serif;
            font-weight: 100;
            transform:scaleY(1.1);
            margin: 0;
            margin-left: 3rem;
        }
    }
}
.index2 {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    
}

.botonmike {

    text-align: center;
    margin: auto;
    margin-top: 2.5rem;
    background-color: $verdeO;
    padding: 1rem;
    border-radius: 3rem;
    a{
        color: $blanco;
        font-weight: $bold;
    }
}
@include telefono {
    .botonmike {
        width: 25%;
    }
}

@include telefono {
    .index2 {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

@include tablet {
    .index2 {
        display: flex;
        flex-direction: row;
    }   
}
.index3 {
    display: grid;
    grid-template-columns:1fr;

    img {
        width: 30rem;
    }
    p {
        font-size: 1.5rem;
        color: $blanco;
        margin: 3rem;
    }
    h1{
        font-size: 2.6rem;
        font-weight: $bold;
    }
}
@include tablet {
    .index3{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

.mikendex4 {
    display: grid;
    grid-template-columns:1fr;
    p {
        font-size: 2rem;
        color: $blanco;
        margin: 5rem;
    }
    img {
        width: 70%;
        margin: auto;
    }
}
@include tablet {
    .mikendex4{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 5rem;
        margin: 3rem;

       img{
            width: 80%;
       } 
       p {
        font-size: 2rem;
        color: $blanco;
        margin: 3rem;
    }
    }
}